//market.js

const mongoose = require("mongoose");

const marketListSchema = new mongoose.Schema({
  Id: Number,
  Category: String,
  Name: String,
  Grade: String,
  Icon: String,
  BundleCount: Number,
  TradeRemainCount: Number, //  null  { type: Number, default: null } 
  YDayAvgPrice: Number,
  RecentPrice: Number,
  CurrentMinPrice: Number,
});

const marketList = mongoose.model("Item", marketListSchema);

module.exports = marketList;
