// loadMarket.js
const axios = require("axios");
const authorizationToken = process.env.API_KEY;
const getPageData = async (pageNo) => {
  const config = {
    method: "post",
    url: "https://developer-lostark.game.onstove.com/auctions/items",
    headers: {
      accept: "application/json",
      authorization: `bearer ${authorizationToken}`,
      "content-Type": "application/json",
    },
    data: {
      ItemLevelMin: 0,
      ItemLevelMax: 0,
      ItemGradeQuality: null,
      SkillOptions: [
        {
          FirstOption: null,
          SecondOption: null,
          MinValue: null,
          MaxValue: null,
        },
      ],
      EtcOptions: [
        {
          FirstOption: null,
          SecondOption: null,
          MinValue: null,
          MaxValue: null,
        },
      ],
      Sort: "BidStart_Price",
      CategoryCode: 210000,
      CharacterClass: "",
      ItemTier: 3,
      ItemGrade: "",
      ItemName: "5",
      PageNo: pageNo,
      SortCondition: "ASC",
    },
  };
  try {
    const response = await axios(config);
    return response.data.Items;
  } catch (error) {
    console.log(error);
    return [];
  }
};
const loadjem = async () => {
  const promises = [];
  for (let pageNo = 0; pageNo <= 10; pageNo++) {
    promises.push(getPageData(pageNo));
  }
  try {
    const resultArrays = await Promise.all(promises);
    const list = resultArrays.flat(); // Flatten the array of arrays
    const newlist = list.map((i) => i.AuctionInfo.BuyPrice);
    const sum = newlist.reduce((total, num) => {
      if (num !== null && num < 3000) {
        return total + num;
      } else {
        return total;
      }
    }, 0);
    function countNonNullElements(arr) {
      let count = 0;
      for (let i = 0; i < arr.length; i++) {
        if (arr[i] !== null && arr[i] < 3000) {
          count++;
        }
      }
      return count;
    }
    const nonNullCount = countNonNullElements(newlist);
    const avg = sum / nonNullCount;
    return avg;
  } catch (error) {
    console.log(error);
    return [];
  }
};
